import React from "react";

const Trademark = () =>{
    const footerStyle={
        width: "100%",
        position:"absolute",
        top:0
    }
    const pStyle = {
        margin:"1%"
      };
return <footer style={footerStyle} className="trademark"><p style={pStyle}>KATRIN SALAC Â© 2019</p></footer>
};

export default Trademark;



